@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&family=Roboto+Mono&family=Roboto+Slab&family=Satisfy&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Signika+Negative&display=swap);
* {
	padding: 0;
	margin: 0%;
	box-sizing: border-box;
}

.app {
	width: 100%;
	height: auto;
	background-color: #c5dae4;
}
.app.dark {
	background: linear-gradient(
		142deg,
		rgba(29, 52, 97, 1) 46%,
		rgba(81, 98, 148, 1) 94%
	);
}
.app.dark p {
	color: #c5dae4;
}
.app.dark h3 {
	color: black;
}
.app.dark h4 {
	color: #568ef8;
}
.app.dark .projects a {
	color: burlywood;
}
.app.dark .about_project a {
	color: burlywood;
}
.main_content {
	max-width: 1440px;
	margin-left: auto;
	margin-right: auto;
	padding: 0 100px;
}
@media (max-width: 800px) {
	.main_content {
		padding: 0 50px;
	}
}
@media (max-width: 500px) {
	.main_content {
		padding: 0 20px;
	}
}

.navbar {
	height: 80px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	width: 100%;
	padding: 20px 50px;
}
.navbar img {
	height: 50px;
	margin-top: auto;
	margin-bottom: auto;
}
.toggle_button {
	margin-top: auto;
	margin-bottom: auto;
	height: 30px;
	width: 80px;
	border-radius: 20px;
	background-image: linear-gradient(aqua, skyblue);
	position: relative;
	cursor: pointer;
	-webkit-transform: scale(0.5);
	        transform: scale(0.5);
}
.notch {
	height: 35px;
	width: 35px;
	border-radius: 50%;
	background: yellow;
	position: absolute;
	top: -2.5px;
	box-shadow: 0 0 5px yellow;
	z-index: 10;
	transition: all 0.4s ease-in-out;
}
.cloud {
	position: absolute;
	background: whitesmoke;
	border-radius: 50%;
	transition: all 0.4s ease-in-out;
}
.sm {
	height: 2px;
	width: 10px;
	top: 20%;
	left: 60%;
}
.md {
	height: 4px;
	width: 15px;
	top: 30%;
	left: 40%;
}
.lg {
	height: 4px;
	width: 30px;
	top: 60%;
	left: 50%;
}
.night {
	background-image: linear-gradient(rgb(80, 80, 175), rebeccapurple);
}
.night .cloud {
	background-color: lightgray;
	box-shadow: 0 0 5px 1px violet;
}
.night > .notch {
	background: rgb(151, 149, 149);
	box-shadow: 0 0 5px rgb(58, 58, 58);
	-webkit-transform: translate(50px, 0);
	        transform: translate(50px, 0);
}
.night > .cloud_container .sm {
	height: 3px;
	width: 3px;
	-webkit-transform: translate(-15px, 2px);
	        transform: translate(-15px, 2px);
}
.night > .cloud_container .sm:first-of-type {
	height: 3px;
	width: 3px;
	-webkit-transform: translate(-30px, 15px);
	        transform: translate(-30px, 15px);
}
.night > .cloud_container .md {
	height: 5px;
	width: 5px;
	-webkit-transform: translate(-15px, 0);
	        transform: translate(-15px, 0);
}
.night > .cloud_container .lg {
	height: 7px;
	width: 7px;
	-webkit-transform: translate(-10px, -2px);
	        transform: translate(-10px, -2px);
}
@media (max-width: 800px) {
	.navbar {
		padding: 10px 20px;
	}
}
@media (max-width: 400px) {
	.navbar {
		padding: 10px 10px;
	}
}

.social_handles {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	position: fixed;
	bottom: 20px;
	left: 20px;
}

.social_handles a {
	text-decoration: none;
	color: #588ef7 !important;
	margin-bottom: 20px;
}
.social_handles a:nth-child(4) {
	font-family: "Dancing Script", cursive;
	font-weight: bolder;
}
.social_handles a:nth-child(even) {
	-webkit-animation: popIn 1.5s ease-in-out 1;
	        animation: popIn 1.5s ease-in-out 1;
}
.social_handles a:nth-child(odd) {
	-webkit-animation: popInodd 1.5s ease-in 1;
	        animation: popInodd 1.5s ease-in 1;
}
@-webkit-keyframes popInodd {
	0% {
		margin-right: -50px;
		-webkit-transform: scale(0);
		        transform: scale(0);
		opacity: 0;
	}
	40% {
		opacity: 0.2;
	}
	90% {
		-webkit-transform: scale(1.2);
		        transform: scale(1.2);
	}
	100% {
		margin-right: 0%;
		opacity: 1;
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}
@keyframes popInodd {
	0% {
		margin-right: -50px;
		-webkit-transform: scale(0);
		        transform: scale(0);
		opacity: 0;
	}
	40% {
		opacity: 0.2;
	}
	90% {
		-webkit-transform: scale(1.2);
		        transform: scale(1.2);
	}
	100% {
		margin-right: 0%;
		opacity: 1;
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}
@-webkit-keyframes popIn {
	0% {
		margin-left: -50px;
		-webkit-transform: scale(0);
		        transform: scale(0);
		opacity: 0;
	}
	40% {
		opacity: 0.2;
	}
	90% {
		-webkit-transform: scale(1.2);
		        transform: scale(1.2);
	}
	100% {
		margin-right: 0%;
		opacity: 1;
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}
@keyframes popIn {
	0% {
		margin-left: -50px;
		-webkit-transform: scale(0);
		        transform: scale(0);
		opacity: 0;
	}
	40% {
		opacity: 0.2;
	}
	90% {
		-webkit-transform: scale(1.2);
		        transform: scale(1.2);
	}
	100% {
		margin-right: 0%;
		opacity: 1;
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}
@media (max-width: 800px) {
	.social_handles {
		display: none;
	}
}

.intro {
	margin-top: 200px;
	height: calc(100vh - 280px);
}
.intro h3 {
	margin-bottom: 10px;
	font-family: "Roboto Mono", monospace;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	font-size: 16px;
	-webkit-animation: fadeIn 0.5s ease-in 1;
	        animation: fadeIn 0.5s ease-in 1;
}

.intro h3 img {
	width: 30px;
}
.intro h3 img:hover {
	-webkit-animation: wave 1s linear infinite;
	        animation: wave 1s linear infinite;
}
@-webkit-keyframes wave {
	0% {
		-webkit-transform: rotate(0);
		        transform: rotate(0);
	}
	25% {
		-webkit-transform: rotate(10deg);
		        transform: rotate(10deg);
	}
	50% {
		-webkit-transform: rotate(-10deg);
		        transform: rotate(-10deg);
	}
	75% {
		-webkit-transform: rotate(15deg);
		        transform: rotate(15deg);
	}
	100% {
		-webkit-transform: rotate(-15deg);
		        transform: rotate(-15deg);
	}
}
@keyframes wave {
	0% {
		-webkit-transform: rotate(0);
		        transform: rotate(0);
	}
	25% {
		-webkit-transform: rotate(10deg);
		        transform: rotate(10deg);
	}
	50% {
		-webkit-transform: rotate(-10deg);
		        transform: rotate(-10deg);
	}
	75% {
		-webkit-transform: rotate(15deg);
		        transform: rotate(15deg);
	}
	100% {
		-webkit-transform: rotate(-15deg);
		        transform: rotate(-15deg);
	}
}
.intro h1 {
	font-size: clamp(40px, 8vw, 80px);
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	font-family: "Roboto Slab", serif;
	margin-bottom: 20px;
	-webkit-animation: fadeIn 0.6s ease-in 1;
	        animation: fadeIn 0.6s ease-in 1;
}
.intro p {
	width: 40%;
	font-size: 20px;
	font-family: "Signika Negative", sans-serif;
	-webkit-animation: fadeIn 0.7s ease-in 1;
	        animation: fadeIn 0.7s ease-in 1;
}
.intro button {
	margin-top: 50px;
	margin-left: 40px;
	padding: 10px 40px;
	font-size: 20px;
	border: none;
	border-radius: 10px;
	cursor: pointer;
	background-color: #eca06d;
	font-family: "Roboto Slab", serif;
	font-weight: bolder;
	color: #1d3461;
	-webkit-animation: fadeIn 0.8s ease-in 1;
	        animation: fadeIn 0.8s ease-in 1;
}
.intro button:hover {
	background-color: #f7ba92;
	border: 1px solid #ec5f01;
	transition: all 0.2s ease-in-out;
	-webkit-transform: skew(-5deg);
	        transform: skew(-5deg);
}
@-webkit-keyframes fadeIn {
	0% {
		-webkit-transform: translateY(-10%) rotateX(45deg);
		        transform: translateY(-10%) rotateX(45deg);
		opacity: 0;
	}
	30% {
		opacity: 0;
	}
	100% {
		-webkit-transform: rotateX(0deg);
		        transform: rotateX(0deg);
		opacity: 1;
	}
}
@keyframes fadeIn {
	0% {
		-webkit-transform: translateY(-10%) rotateX(45deg);
		        transform: translateY(-10%) rotateX(45deg);
		opacity: 0;
	}
	30% {
		opacity: 0;
	}
	100% {
		-webkit-transform: rotateX(0deg);
		        transform: rotateX(0deg);
		opacity: 1;
	}
}
@media (min-width: 1000px) and (max-width: 1024px) {
	.intro {
		margin-top: 300px;
	}
	.intro h1 {
		width: 60%;
	}
	.intro p {
		width: 80%;
	}
}
@media (max-width: 800px) {
	.intro p {
		width: 85%;
	}
}
@media (max-width: 700px) {
	.intro {
		margin-left: 20px;
		margin-top: 60px;
		height: calc(100vh - 140px);
	}
	.intro p {
		width: 85%;
	}
}

.about_me {
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	margin-bottom: 200px;
}
.left {
	-webkit-flex: 0.8 1;
	        flex: 0.8 1;
}
.about_me h3 {
	font-size: 30px;
	color: #1f487e;
	font-family: "Roboto Slab", serif;
	margin-bottom: 30px;
	opacity: 0;
	transition: all 1s ease-in-out;
}
.about_me .h3_inview {
	opacity: 1;
}
.about_me p {
	padding-left: 10px;
	font-size: 20px;
	font-family: "Signika Negative", sans-serif;
	margin-bottom: 20px;
	opacity: 0;
	transition: all 1s ease-in-out;
	transition-delay: 1s;
}
.about_me .p_inview {
	opacity: 1;
}
.about_me img {
	opacity: 0;
	-webkit-transform: scale(0.6);
	        transform: scale(0.6);
}
.about_me .img_inview {
	opacity: 1;
	-webkit-transform: scale(1);
	        transform: scale(1);
}
.about_me .mypic {
	height: 300px;
	border-radius: 20px;
	-webkit-filter: blur(1px);
	        filter: blur(1px);
	transition: all 0.5s ease-in-out;
}
.about_me .mypic:hover {
	-webkit-filter: blur(0px);
	        filter: blur(0px);
	-webkit-transform: scale(0.96);
	        transform: scale(0.96);
}
@media (max-width: 700px) {
	.about_me {
		-webkit-flex-direction: column;
		        flex-direction: column;
		margin-left: 20px;
		margin-right: 20px;
		-webkit-align-items: center;
		        align-items: center;
	}
	.about_me .mypic {
		width: 180px;
		height: auto;
	}
}

.projects {
	text-align: center;
}
.projects h3 {
	text-align: left;
	font-size: 30px;
	color: #1f487e;
	opacity: 0;
	transition: all 1s ease-in-out;
	font-family: "Roboto Slab", serif;
}
.projects .h3_inview {
	opacity: 1;
}
.projects_container {
	padding: 30px 0px;
}
.projects_dis {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
	padding: 50px 0px 0px 0px;
	width: 100%;
	position: relative;
	opacity: 0;
	-webkit-transform: scale(1.3);
	        transform: scale(1.3);
	transition: all 1s ease-in-out;
}
.project_inview {
	opacity: 1;
	-webkit-transform: scale(1);
	        transform: scale(1);
}
.projects_dis img {
	width: 60%;
	max-width: 600px;
	border-radius: 3px;
}
.about_project {
	width: 60%;
	max-width: 400px;
	text-align: center;
	margin-top: auto;
	margin-bottom: auto;
}
.about_project h5 {
	font-size: 16px;
	font-family: "Satisfy", cursive;
}
.about_project h4 {
	font-size: 20px;
	padding-bottom: 10px;
	font-family: "Roboto Slab", serif;
	color: #b9314f;
}
.about_project p {
	font-size: 17px;
	padding: 20px 15px 20px 15px;
	font-family: "Signika Negative", sans-serif;
	border-radius: 3px;
	margin-bottom: 10px;
}
.about_project a {
	color: #b9314f;
	padding: 0 10px;
}
.projects_dis:nth-child(odd) {
	-webkit-flex-direction: row-reverse;
	        flex-direction: row-reverse;
}

.projects_more {
	font-family: "Roboto Slab", serif;
	text-decoration: underline;
	color: #b9314f;
	font-size: 30px;
}
@media screen and (max-width: 500px) {
	.projects h3 {
		text-align: center;
		font-size: 24px;
		padding-bottom: 20px;
	}
	.projects_dis img {
		display: none;
	}
	.about_project {
		padding: 30px 0px;
		width: 100%;
		background-color: rgba(81, 98, 148, 0.4);
	}
}

